import { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import { getData, postData } from "../api";
import { getChatHistory, getItemDetailUrl, getItemImages, PREV_IMG,deleteItemUrl } from "../constants/endPoints";
import { convertFromBase64, setLoadingState } from "../constants/helper";
import { getItemData } from "../Hooks/useItemParams";
import DataNotFound from "./DataNotFound";
import useConsole from "../Hooks/useConsole";
import { Spinner } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faEnvelopeOpen, faInbox, faMailBulk, faPhone, faStar } from "@fortawesome/free-solid-svg-icons";
import { FaEnvelopeOpen, FaInbox, FaTrash } from "react-icons/fa";
import { getCurrentUserId } from "../constants/sessionHelper";
import { notify } from "../Components/AppNotification";
import { Modal, Button, Space } from 'antd';


const ProductDetail = () => {
    const { id } = useParams();
    // console.log(id);
    const [productId, setProductId] = useState((prev) => convertFromBase64(id));
    const [productDetails, setProductDetail] = useState({ data: null, isLoading: false });
    const [images, setImages] = useState([]);
    const [confirm, setConfirm] = useState(false);
    const history = useHistory();
    useConsole("productDetails", productDetails);
    const showModal = () => {
        setConfirm(true);
    }

    const hideModal = () => {
        setConfirm(false);
    }

    const getProductData = async () => {
        try {
            setProductDetail((prev) => { return { ...setLoadingState(prev, true) } });
            let res = await getData(getItemDetailUrl("get", 10, 0, productId));
            setProductDetail((prev) => { return { ...setLoadingState(prev, false, res.data) } });
            let imagesRes = await getData(getItemImages(productId));
            console.log(imagesRes);
            setImages((prev) => { return [...imagesRes.data]; })

            setTimeout(() => {
                window.onProductDetailLoaded();
            });
            this.forceUpdate()
        } catch (e) {
            console.log(e);
            setProductDetail((prev) => { return { ...setLoadingState(prev, false) } });
        }
    }
    const startChat = async () => {
        try {
            let data = {
                item_id: productDetails.data.id,
                buyer_user_id: getCurrentUserId(),
                seller_user_id: productDetails.data.added_user_id
            }
            notify("Please Wait", "starting chat....", 'info');
            let res = await postData(getChatHistory(), data);
            console.log(res.data);
            history.push({ pathname: '/chats', chat: res.data });
        } catch (e) {
            notify('Error', 'unable to start chat', 'error');
        }
    }
    const deleteItem = async () => {
        // items/item_delete/api_key/teampsisthebest
        const data = { item_id: productDetails.data.id };
        let res = await postData(deleteItemUrl(), data);
        console.log(res.data);
        history.push('/');

    }
    useEffect(() => {
        setProductId(convertFromBase64(id));
    }, [])
    useEffect(() => {
        if (productId != null) {
            getProductData();
        }
    }, [productId])
    return (
        <div className="product-detail-page">
            {/* {console.log(productId && productDetail.data)} */}
            <Modal
                title="Confirm?"
                visible={confirm}
                onOk={deleteItem}
                onCancel={hideModal}
                okText="Yes"
                cancelText="Cancel"
            >
                <p>Are you sure you want to delete this post?</p>
            </Modal>
            {(productId && productDetails.data != null) ? <main>
                {productDetails.data.added_user_id != getCurrentUserId() && <div style={
                    {
                        position: 'fixed',
                        bottom: 10,
                        right: 10,
                        display: 'flex',
                        cursor: 'pointer',
                        zIndex: 3,
                        border: "none"
                    }
                }>
                    <button onClick={startChat} style={{
                        padding: '1em',
                        backgroundColor: '#930002',
                        color: 'white',
                        borderColor: 'black',
                        borderRadius: 100,
                        zIndex: 1,
                        boxShadow: '#8080803d 0px 4px 20px 6px',
                    }}><FaEnvelopeOpen size={30} /></button>
                </div>}
                {productDetails.data.added_user_id == getCurrentUserId() && <div style={
                    {
                        position: 'fixed',
                        bottom: 10,
                        right: 10,
                        display: 'flex',
                        cursor: 'pointer',
                        zIndex: 3,
                        border: "none"
                    }
                }>
                    <button style={{
                        padding: '1em',
                        backgroundColor: '#930002',
                        color: 'white',
                        borderColor: 'black',
                        borderRadius: 100,
                        zIndex: 1,
                        boxShadow: '#8080803d 0px 4px 20px 6px',
                    }} onClick={showModal}><FaTrash size={30} /></button>
                </div>}
                {/* <!-- Breadcrumbs --> */}
                <div className="b-crumbs-wrap">
                    <div className="cont b-crumbs">
                        <ul>
                            <li>
                                <Link to="/">Motor</Link>
                            </li>
                            <li>
                                <span>{productDetails.data.title}</span>
                            </li>
                        </ul>
                        {/* <div className="b-crumbs-menu">
                            <a id="b-crumbs-menu" href="#"></a>
                            <ul className="b-crumbs-menulist">
                                <li><a href="product.html">Product: Standart</a></li>
                                <li><a href="product-vehicle.html">Product: Vehicle</a></li>
                            </ul>
                        </div> */}
                    </div>
                </div>

                <div className="cont maincont">

                    <h1><span>{productDetails.data.title}</span></h1>
                    <span className="maincont-line1"></span>
                    <span className="maincont-line2"></span>

                    {/* <!-- Product - start --> */}
                    <div className="prod">

                        {/* <!-- Product Slider - start --> */}
                        <div className="prod-slider-wrap">
                            <div className="flexslider prod-slider" id="prod-slider">
                                <ul className="slides">
                                    {images.map((val, i) => {
                                        return <li>
                                            <a data-fancybox-group="prod" className="fancy-img" href={PREV_IMG + val.img_path}><img src={PREV_IMG + productDetails.data.default_photo.img_path} alt="" /></a>
                                        </li>
                                    })}

                                </ul>
                            </div>
                            <div className="flexslider prod-thumbs" id="prod-thumbs">
                                <ul className="slides">
                                    {images.map((val, i) => {
                                        return <li>
                                            <img src={PREV_IMG + val.img_path} alt="" />
                                        </li>
                                    })}

                                </ul>
                            </div>
                        </div>
                        {/* <!-- Product Slider - end --> */}

                        {/* <!-- Product Content - start --> */}
                        <div className="prod-cont">
                            <div className="prod-desc">
                                <p className="prod-desc-ttl"><span>Description</span></p>
                                <p>{productDetails.data.description} <a id="prod-showdesc" href="#">read more</a></p>
                            </div>
                            <div className="prod-props">
                                <dl>
                                    <dt>Uploaded</dt>
                                    <dd>{productDetails.data.added_date_str}</dd>
                                    <dt>Price</dt>
                                    <dd>{productDetails.data.price}
                                        {productDetails.data.item_currency && productDetails.data.item_currency.currency_symbol +
                                            " (" + productDetails.data.item_currency.currency_short_form + ")"}</dd>
                                    <dt>Price Type</dt>
                                    <dd>{productDetails.data.item_price_type.name}</dd>
                                    <dt>Condition</dt>
                                    <dd>{productDetails.data.condition_of_item.name}</dd>
                                    <dt>Location</dt>
                                    <dd>{productDetails.data.item_location.name}</dd>
                                    <dt><a id="prod-showprops" href="#">view all details</a></dt>
                                    <dd></dd>
                                </dl>
                            </div>
                            <div className="prod-info">
                                <div className="prod-price-wrap">
                                    <p>Price Unit</p>
                                    <p className="prod-price">{productDetails.data.price_unit}</p>
                                </div>
                                <div className="prod-qnt-wrap">
                                    <p>Type</p>
                                    <p className="qnt-wrap prod-qnt">
                                        {productDetails.data.fuel_type && productDetails.data.fuel_type.name}
                                    </p>
                                </div>
                                <div className="prod-total-wrap">
                                    <p>Model</p>
                                    <p className="prod-total">{productDetails.data.model.name}</p>
                                </div>
                                <div className="prod-shipping-wrap">
                                    <p>Menufecturer</p>
                                    <p className="prod-shipping">{productDetails.data.manufacturer.name}</p>
                                </div>
                            </div>
                            <div className="prod-actions">

                                <div className="d-flex justify-content-center pt-3">
                                    <h1 style={productDetails.data.is_sold_out == 0 ? { color: "#202020" } : { color: "#ff3100" }}>
                                        {productDetails.data.is_sold_out == 0 ? "Not Sold" : "Sold"}
                                        <span style={{ color: "#ff3100", background: "#ffffff", marginLeft: 10 }}>{" (For " + productDetails.data.sell_type + ")"}</span>
                                    </h1>
                                    {/* <p className="prod-rating">
                                        <i className="fa fa-star-o" title="5"></i><i className="fa fa-star-o" title="4"></i><i className="fa fa-star-o" title="3"></i><i className="fa fa-star-o" title="2"></i><i className="fa fa-star-o" title="1"></i>
                                    </p>
                                    <p><span className="prod-rating-count">89</span> reviews</p> */}
                                </div>
                                {/* <p className="prod-compare">
                                    <a href="#"></a>
                                </p>
                                <p className="prod-favorites">
                                    <a href="#"></a>
                                </p>
                                <p className="prod-add">
                                    <a href="#">Add to cart</a>
                                </p> */}

                            </div>
                        </div>
                        {/* <!-- Product Content - end --> */}

                    </div>
                    {/* <!-- Product - end --> */}

                    {/* <!-- Product Tabs - start --> */}
                    <div className="prod-tabs-wrap">
                        <ul className="prod-tabs">
                            <li data-prodtab-num="1" id="prod-desc" className="active">
                                <a data-prodtab="#prod-tab-1" href="#">Description</a>
                            </li>
                            <li data-prodtab-num="2" id="prod-props">
                                <a data-prodtab="#prod-tab-2" href="#">Features</a>
                            </li>
                            <li data-prodtab-num="3" id="prod-reviews">
                                <a data-prodtab="#prod-tab-3" href="#">User <span>Rating</span></a>
                            </li>
                            {/* <li className="prod-tabs-addreview">Add a review</li> */}
                        </ul>

                        <div className="prod-tab-cont">
                            {/* Description */}
                            <p data-prodtab-num="1" className="prod-tab-mob active" data-prodtab="#prod-tab-1">Description</p>
                            <div className="prod-tab prod-tab-desc" id="prod-tab-1">
                                {productDetails.data.description}
                            </div>
                            <p data-prodtab-num="2" className="prod-tab-mob" data-prodtab="#prod-tab-2">Features</p>
                            <div className="prod-tab" id="prod-tab-2">
                                <dl className="prod-tab-props">
                                    {/* <dt>Stroke</dt>
                                    <dd>4 Stroke</dd>
                                    <dt>Max Power</dt>
                                    <dd>62kW/6000 (r/min)</dd>
                                    <dt>Cylinders</dt>
                                    <dd>2 Cylinder</dd>
                                    <dt>Max Torque</dt>
                                    <dd>101N·m/5000 (r/min)</dd>
                                    <dt>Displacement (ml)</dt>
                                    <dd>1000CC</dd> */}
                                    <dt>Uploaded</dt>
                                    <dd>{productDetails.data.added_date_str}</dd>
                                    <dt>Price</dt>
                                    <dd>{productDetails.data.price}
                                        {productDetails.data.item_currency && productDetails.data.item_currency.currency_symbol +
                                            " (" + productDetails.data.item_currency.currency_short_form + ")"}</dd>
                                    <dt>Price Type</dt>
                                    <dd>{productDetails.data.item_price_type.name}</dd>
                                    <dt>Condition</dt>
                                    <dd>{productDetails.data.condition_of_item.name}</dd>
                                    <dt>Location</dt>
                                    <dd>{productDetails.data.item_location.name}</dd>
                                    <dt>Price Unit</dt>
                                    <dd>{productDetails.data.price_unit}</dd>
                                    <dt>Type</dt>
                                    <dd>{productDetails.data.fuel_type && productDetails.data.fuel_type.name}</dd>
                                    <dt>Manufacturer</dt>
                                    <dd>{productDetails.data.manufacturer && productDetails.data.manufacturer.name}</dd>
                                    <dt>Model</dt>
                                    <dd>{productDetails.data.model.name}</dd>
                                </dl>
                            </div>
                            <p data-prodtab-num="3" className="prod-tab-mob" data-prodtab="#prod-tab-3">Reviews</p>
                            <div className="prod-tab prod-reviews" id="prod-tab-3">
                                <div className="row">
                                    <div className="col-12 user-info">
                                        <div className="row">
                                            <div className="col-2">
                                                <img className="img-oc-w100" src={PREV_IMG + productDetails.data.user.user_profile_photo} />
                                            </div>
                                            <div className="col-8">
                                                <div>
                                                    <h4>{productDetails.data.user.user_name}</h4>
                                                </div>
                                                <div>
                                                    <label><FontAwesomeIcon icon={faPhone} size={10} className="ic-rm4" />{productDetails.data.user.user_phone}</label>
                                                </div>
                                                <div>
                                                    <label><FontAwesomeIcon icon={faEnvelope} size={10} className="ic-rm4" />{productDetails.data.user.user_email}</label>
                                                </div>
                                                <div>
                                                    <label><FontAwesomeIcon icon={faStar} size={10} className="ic-rm4" color="#bca900" />{productDetails.data.user.rating_count}</label>
                                                </div>
                                                {/* <div>
                                                    <label><FontAwesomeIcon icon={faStar} size={10} className="ic-rm4" color="#bca900"/>{productDetails.data.user.rating_count}</label>
                                                </div> */}
                                            </div>
                                        </div>
                                        <div className="col-12 pt-3 pb-3">
                                            <h1>User Rating</h1>
                                        </div>
                                        <div className="row rating-container">

                                            <div className="col-12 row center-text ">
                                                <div className="col-1 right-text">
                                                    <label> {productDetails.data.user.rating_details.one_star_count}</label>
                                                </div>
                                                <div className="col-6 left-text">
                                                    {
                                                        [...Array(1)].map((val, i) => {
                                                            return <FontAwesomeIcon icon={faStar} size={20} className="ic-rm4-f10" color="#bca900" />
                                                        })
                                                    }<label>{productDetails.data.user.rating_details.five_star_percent}%</label>
                                                </div>

                                            </div>
                                            <div className="col-12 row center-text">
                                                <div className="col-1 right-text">
                                                    <label>{productDetails.data.user.rating_details.two_star_count}</label>
                                                </div>
                                                <div className="col-6 left-text">
                                                    {
                                                        [...Array(2)].map((val, i) => {
                                                            return <FontAwesomeIcon icon={faStar} size={20} className="ic-rm4-f10" color="#bca900" />
                                                        })
                                                    }<label>{productDetails.data.user.rating_details.five_star_percent}%</label>
                                                </div>
                                            </div>
                                            <div className="col-12 row center-text">
                                                <div className="col-1 right-text">
                                                    <label>{productDetails.data.user.rating_details.three_star_count}</label>
                                                </div>
                                                <div className="col-6 left-text">
                                                    {
                                                        [...Array(3)].map((val, i) => {
                                                            return <FontAwesomeIcon icon={faStar} size={20} className="ic-rm4-f10" color="#bca900" />
                                                        })
                                                    }<label>{productDetails.data.user.rating_details.five_star_percent}%</label>
                                                </div>
                                            </div>
                                            <div className="col-12 row center-text">
                                                <div className="col-1 right-text">
                                                    <label>{productDetails.data.user.rating_details.four_star_count}</label>
                                                </div>
                                                <div className="col-6 left-text">
                                                    {
                                                        [...Array(4)].map((val, i) => {
                                                            return <FontAwesomeIcon icon={faStar} size={20} className="ic-rm4-f10" color="#bca900" />
                                                        })
                                                    }<label>{productDetails.data.user.rating_details.five_star_percent}%</label>
                                                </div>
                                            </div>
                                            <div className="col-12 row">
                                                <div className="col-1 right-text">
                                                    <label>{productDetails.data.user.rating_details.five_star_count}</label>
                                                </div>
                                                <div className="col-6 left-text">
                                                    {
                                                        [...Array(5)].map((val, i) => {
                                                            return <FontAwesomeIcon icon={faStar} size={20} className="ic-rm4-f10" color="#bca900" />
                                                        })
                                                    }<label>{productDetails.data.user.rating_details.five_star_percent}%</label>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                            {/* <p className="prod-tabs-addreview prod-tabs-addreview-mob">Add a review</p> */}
                        </div>
                    </div>
                    {/* <!-- Product Tabs - end --> */}

                </div>

            </main>
                : productDetails.isLoading ? <div className="d-flex justify-content-center align-items-center mt-8" style={{ height: 400 }}>
                    <Spinner animation="grow" />
                </div> : <DataNotFound />}
        </div>
    );
}

export default ProductDetail;