import { useEffect, useState } from "react";
import { useLocation } from "react-router";
import { Link } from "react-router-dom";
import { postData } from "../api";
import { getItemsUrl, PREV_IMG } from "../constants/endPoints";
import { convertToBase64, setLoadingState } from "../constants/helper";
import { getItemData } from "../Hooks/useItemParams";
import {
    slice, concat,
} from 'lodash';
import DataNotFound from "./DataNotFound";

const ProductList = () => {
    // console.log("data",itemData);
    // console.log("props",data);
    const [offset, setOffset] = useState(0);
    const [limit, setLimit] = useState(10);
    const [products, setProduct] = useState({ isLoading: false, data: [] });
    const [loadMoreTxt, setLoadMoreTxt] = useState("Load More");
    // const [isError, setError] = useState(false);
    const location = useLocation();
    // const [itemData,setItemData] = useState(itemData);
    // let itemData = null;
    let reqData = null;
    // const history = usePlaceholder

    const getPageData = async () => {
        try {
            if (!reqData) {
                reqData = getItemData();
            }
            setProduct((prev) => { return { ...setLoadingState(prev, true) } });
            const res2 = await postData(getItemsUrl("search", limit, offset), reqData);
            let response = res2.data;
            console.log(response);
            setProduct((prev) => { return { ...setLoadingState(prev, false, response) } });

            // var res = await postData(getItemsUrl("search",limit,offset));
        } catch (e) {
            setProduct((prev) => { return { ...setLoadingState(prev, false) } });
            console.log(e);
        }
    }
    const loadMoreData = async () => {
        try {
            if (!reqData) {
                reqData = getItemData();
            }
            setLoadMoreTxt("Loading....");
            // window.addEventListener('scroll', loadMoreTxt);
            // setProduct((prev) => { return { ...setLoadingState(prev, true) } });
            const res2 = await postData(getItemsUrl("search", limit, offset), reqData);
            let response = res2.data;

            setProduct((prev) => { var ndata = concat(prev.data, response); return { data: ndata, isLoading: false }; });
            if (res2.data.length < 1) {
                setLoadMoreTxt("No More Data");
            }
            // window.scrollTo(0, 0)
            setLoadMoreTxt("Load More");
            console.log(response);

            // setProduct((prev) => { return { ...setLoadingState(prev, false, response) } });
        } catch (e) {
            setLoadMoreTxt("No More Data");
            console.log(e);
        }

    }
    const handleLoadMore = async () => {
        setOffset(offset + limit);
    }

    useEffect(() => {
        if (offset != 0)
            loadMoreData();
    }, [offset, limit]);

    useEffect(() => {
        window.scrollTo(0, 0);
        console.log("itemdata",location.itemData);
        reqData = location.itemData;
        getPageData();
    }, []);
    return (
        <main className="productList">

            {/* <!-- Breadcrumbs --> */}
            <div className="b-crumbs-wrap">
                <div className="cont b-crumbs">
                    <ul>
                        <li>
                            <Link to="/">Motor</Link>
                        </li>
                     
                        <li>
                            <span>Products</span>
                        </li>
                    </ul>
            
                </div>
            </div>
            <div>
                <div className="cont specials">

                    <h2 style={{position:"relative"}}><span>Main parts</span></h2>
                    

                    {/* <!-- Catalog Sections --> */}

                    {/* <!-- Catalog Filter - end --> */}

                    {/* <!-- Catalog Items - start --> */}
                   
                    <div className="section-list row">
                        {
                            !products.isLoading && products.data.length < 1 ?<DataNotFound hideTopBar={true} />:<div></div>
                        }
                        {
                            products.data && products.data.map((val, i) => {
                                return <div className="sectls" key={i}>
                                    <Link to={"/product_details/" + convertToBase64(val.id)} className="sectls-img">
                                        <img src={PREV_IMG + val.default_photo.img_path} onError={(e) => { e.target.src = "/assets/images/placeholder.jpeg" }} />

                                       
                                    </Link>
                                    <div className="sectls-cont">
                                        <div className="sectls-ttl-wrap">
                                            {/* <p><a href="#">Helmets</a></p> */}
                                            <h3><Link to={"/product_details/" + convertToBase64(val.id)}>{val.title}</Link></h3>
                                        </div>
                                        <div className="sectls-price-wrap">
                                            <p>Price</p>
                                            <p className="sectls-price">{val.price} {val.item_currency && val.item_currency.currency_symbol +
                                                " (" + val.item_currency.currency_short_form + ")"}</p>
                                        </div>
                                        <div className="sectls-qnt-wrap">
                                            <p>Price Type</p>
                                            <p className="sectls-price">{val.item_price_type.name}</p>
                                           
                                        </div>
                                        <div className="sectls-total-wrap">
                                            <p>Location</p>
                                            <p className="sectls-total">{val.item_location.name}</p>
                                        </div>
                                    </div>
                                    <div className="sectls-info">
                                        <div className="sectls-rating-wrap">
                                            <p className="sectls-rating">
                                                <i className="fa fa-star-o" title="5"></i><i className="fa fa-star-o" title="4"></i><i className="fa fa-star-o" title="3"></i><i className="fa fa-star-o" title="2"></i><i className="fa fa-star-o" title="1"></i>
                                            </p>
                                            <p><span className="sectls-rating-count">89</span> reviews</p>
                                        </div>
                                        <p className="sectls-id">id 5376445</p>
                                        
                                        <p className="sectls-favorites">
                                            <a href="#"></a>
                                        </p>
                                        
                                    </div>
                                </div>

                            })
                        }

                    </div>
                    {/* <!-- Catalog Items - end --> */}

                    {/* <!-- Pagination --> */}
                    <div className="lbl-load-more">
                        <label onClick={handleLoadMore}>{loadMoreTxt}</label>
                    </div>

                </div>
            </div>

            {/* <!-- Get a Special Deals --> */}
            <div className="getspec-wrap">
                <div className="cont getspec">
                    <div className="getspec-cont">
                        <h3>Winter is coming</h3>
                        <p>New snowmobile parts</p>
                        <form action="#">
                            <input type="text" placeholder="Email address" />
                            <input type="submit" value="Get a special deals" />
                        </form>
                    </div>
                    <a href="#" className="getspec-img">
                        <img src="img/getspec.jpg" alt="" />
                    </a>
                </div>
            </div>

        </main>
    );
}

export default ProductList;