
import React, { useState, useEffect, useCallback } from 'react';
import { BASE_URL, getBlogsUrl, getCategoriesUrl, getItemsUrl, getMenufecturerUrl, getRecentProductsUrl, PREV_IMG } from '../../constants/endPoints';
import { getData, postData } from '../../api';
import { Spinner } from 'react-bootstrap';
import { useHistory } from 'react-router';
import { getItemData } from '../../Hooks/useItemParams';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLocationArrow, faMapMarked, faMapPin, faSearchLocation } from '@fortawesome/free-solid-svg-icons';
import useConsole from '../../Hooks/useConsole';
import BlogSlider from './BlogSlider';
import { convertFromBase64,convertToBase64 } from '../../constants/helper';
import { Link } from 'react-router-dom';
// import { Spinner } from 'bootstrap';
const Home = () => {
    const [categories, setCategories] = useState({ data: [], isLoading: false });
    const [recentProducts, setRecentProducts] = useState({ data: [], isLoading: false });
    const [popularProducts, setPopularProducts] = useState({ data: [], isLoading: false });
    const [menufecturers, setMenufecturers] = useState({ data: [], isLoading: false });
    const [blogs, setBlogs] = useState({ data: [], isLoading: false });
    const history = useHistory();

    useConsole("categories", categories);
    useConsole("popularProducts", popularProducts);
    const getCategories = async () => {
        try {
            setCategories({ isLoading: true });
            const res = await getData(getCategoriesUrl("get", 30, 0));
            setCategories({ data: res.data, isLoading: false });
        } catch (e) {
            setCategories({ data: [], isLoading: false });
            console.log(e);
        }
    }
    const getBlogs = async () => {
        try {
            setBlogs({ isLoading: true });
            const res = await getData(getBlogsUrl("get", 30, 0));
            setBlogs({ data: res.data, isLoading: false });
        } catch (e) {
            setBlogs({ data: [], isLoading: false });
            console.log(e);
        }
    }
    const getMenufecturers = async () => {
        try {
            setMenufecturers({ isLoading: true });
            const res = await getData(getMenufecturerUrl("get", 30, 0));
            setMenufecturers({ data: res.data, isLoading: false });
        } catch (e) {
            console.log(e);
        }
    }
    const getPopularProducts = async () => {
        try {
            setPopularProducts((data) => { data.isLoading = false; return data; });
            let itemParams = getItemData();
            itemParams.order_by = "touch_count";
            itemParams.is_paid = "";
            const res2 = await postData(getItemsUrl("search", 12, 0), itemParams);
            setPopularProducts((prev) => {
                return { data: res2.data, isLoading: false }
            });
        } catch (e) {
            console.log(e);
            setPopularProducts((data) => { data.isLoading = false; return data; });
        }
    }
    const getRecentProducts = async () => {
        try {
            setRecentProducts({ isLoading: true });
            let itemParams = getItemData();
            itemParams.order_by = "added_date";
            itemParams.is_paid = "paid_item_first";
            const res2 = await postData(getItemsUrl("search", 12, 0), itemParams);
            setRecentProducts((prev) => { return { data: res2.data, isLoading: false } });
            // console.log("recent", res2);
        } catch (e) {
            console.log(e);
            setRecentProducts({ isLoading: false });
        }
    }
    const getCategoryItems = (fnData) => {
        let itmData = getItemData();
        itmData.category_id = fnData.category_id;
        itmData.is_paid = "paid_item_first";
        // is_paid
        history.push({pathname:"products",itemData:itmData});
    }
    const getMenufecturerItems = (id) =>{
        // manufacturer_id
        let itmData = getItemData();
        itmData.manufacturer_id = id;
        itmData.is_paid = "paid_item_first";
        // is_paid
        history.push({pathname:"products",itemData:itmData});
    }

    const getPageData = useCallback(
        () => {
            getCategories();
            getMenufecturers();
            getRecentProducts();
            getPopularProducts();
            getBlogs();
            setTimeout(()=>{
                window.onHome();
            });

        },
        [],
    )
    // async ()=>{


    // }
    useEffect(async () => {
        getPageData();
    }, [])
    return (
        <div className="home-page">
            <main>
                <div className="frontslider-wrap">
                    <div className="responisve-container">
                        <div className="slider" id="front-slider">
                            <div className="slide">
                                <img src="/external_assets/img/front-slider/1.png"
                                    data-position="45,430" data-in="fade" data-delay="0" data-out="fade" alt="American Legends" />
                                <p className="caption1"
                                    data-position="50,0" data-in="right" data-step="0" data-out="fade" data-delay="0">AMERICAN</p>
                                <p className="caption2"
                                    data-position="150,0" data-in="right" data-step="0" data-out="fade" data-delay="0">LEGENDS</p>
                            </div>
                            <div className="slide">
                                <img src="/external_assets/img/front-slider/2.png"
                                    data-position="50,518" data-in="fade" data-delay="0" data-out="fade" alt="Legendary Bike" />
                                <p className="caption1"
                                    data-position="50,0" data-in="right" data-step="0" data-out="fade" data-delay="0">OFFROAD</p>
                                <p className="caption2"
                                    data-position="150,0" data-in="right" data-step="0" data-out="fade" data-delay="0">KING</p>
                            </div>
                            <div className="slide">
                                <img src="/external_assets/img/front-slider/3.png"
                                    data-position="21,480" data-in="fade" data-delay="0" data-out="fade" alt="Dream Car" />
                                <p className="caption1"
                                    data-position="50,0" data-in="right" data-step="0" data-out="fade" data-delay="0">DREAM</p>
                                <p className="caption2"
                                    data-position="150,0" data-in="right" data-step="0" data-out="fade" data-delay="0">CARS</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="cont home-cat">
                    <div className="row mob-scroll mob-scroll-row-h180">

                        {
                            categories.isLoading &&
                            <div className="d-flex justify-content-center align-items-center mt-8">
                                <Spinner animation="grow" />
                            </div>
                        }
                        {
                            categories.data &&
                            categories.data.map((val, i) => {
                                return <div onClick={()=>{getCategoryItems(val)}} className="col-sm-3 col-lg-2 col-md-3 mob-scroll-item-h180" key={i} >
                                    <div className="d-flex justify-content-center flex-column align-items-center">
                                        <div className="frontcategs-img">
                                            <img src={BASE_URL + val.category_image} alt="" onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }}/>
                                        </div>
                                        <div>
                                            <label>{val.category_name}</label>
                                        </div>

                                    </div>
                                </div>
                            })
                        }

                    </div>
                </div>
                <div className="cont specials">
                    <h2>Menufecturers</h2>
                    <div className="row mob-scroll mob-scroll-row-h180">
                        {
                            menufecturers.isLoading &&
                            <div className="d-flex justify-content-center align-items-center mt-8">
                                <Spinner animation="grow" />
                            </div>
                        }
                        {
                            menufecturers.data &&
                            menufecturers.data.map((val, i) => {
                                return <div className="col-lg-2 col-md-2 col-sm-4 mob-scroll-item-h180" onClick={()=>{getMenufecturerItems(val.id)}}>
                                    <div className="home-menuf-item d-flex flex-column justify-content-center align-items-center">
                                        <div className="home-menuf-img">
                                            <img className="home-menuf-img" src={PREV_IMG + val.default_icon.img_path} onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }} alt="" />
                                        </div>
                                        <p className='home-menuf-name'>{val.name}</p>
                                    </div>
                                </div>
                            })
                        }

                    </div>
                </div>


                {/* <!-- Frontpage Filter --> */}


                {/* <!-- Special Deals Items --> */}
                <div className="specials-wrap">
                    <div className="cont specials">
                        <h2>Recent Products</h2>
                        <p className="specials-count">{recentProducts.data && "Total Items: " + recentProducts.data.length}</p>
                        <div className="specials-list">
                            <div className="special special-pseudo">
                                <a href="#" className="special-link"></a>
                            </div>
                            {
                                recentProducts.isLoading &&
                                <div className="d-flex justify-content-center align-items-center mt-8">
                                    <Spinner animation="grow" />
                                </div>
                            }
                            <div className="row mob-scroll mob-scroll-row-h330">
                                {
                                    recentProducts.data &&
                                    recentProducts.data.map((val, i) => {
                                        return <div className="special col-lg-3 col-md-4" key={i + "recent"}>
                                            <div className="home-item-profile-container row">
                                                <div className="col-3">
                                                    <img className="home-item-profile-img " src={PREV_IMG + val.user.user_profile_photo} onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }} />
                                                </div>
                                                <div className="col-8">
                                                    <h6 className="home-item-profile-name ">{val.user.user_name}</h6>
                                                    <h6 className="home-item-post-time ">{val.added_date_str}</h6>
                                                </div>
                                            </div>
                                            <Link to={"/product_details/"+convertToBase64(val.id)} className="special-link">
                                                <p className="special-img">
                                                    <img className="home-item-img" src={PREV_IMG + val.default_photo.img_path} alt="" onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }} />
                                                </p>
                                                <h3><span>{val.title}</span></h3>
                                            </Link>
                                            <div className="row home-item-info">
                                                <div className="col-12">
                                                    <h6 className="special-categ">{"$ " + val.price} ({val.condition_of_item.name})</h6>
                                                </div>
                                                <div className="col-12">
                                                    <span className="home-item-icon"><FontAwesomeIcon icon={faMapMarked} /></span>
                                                    {val.item_location.name}
                                                </div>

                                            </div>
                                            {/* <p className="home-item-location">
                                               </p> */}
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                        <p className="special-more">
                            <Link to="/products">show more</Link>
                        </p>
                        <span className="special-line1"></span>
                        <span className="special-line2"></span>
                    </div>
                </div>
                {/* Get Popular Product */}
                <div className="specials-wrap">
                    <div className="cont specials">
                        <h2>Popular Products</h2>
                        <p className="specials-count">{popularProducts.data && "Total Items: " + popularProducts.data.length}</p>
                        <div className="specials-list">
                            <div className="special special-pseudo">
                                <a href="#" className="special-link"></a>
                            </div>
                            {
                                popularProducts.isLoading &&
                                <div className="d-flex justify-content-center align-items-center mt-8">
                                    <Spinner animation="grow" />
                                </div>
                            }
                            <div className="row mob-scroll mob-scroll-row-h330">
                                {
                                    popularProducts.data &&
                                    popularProducts.data.map((val, i) => {
                                        return <div className="special col-lg-3 col-md-4" key={i + "popular"}>
                                            <div className="home-item-profile-container row">
                                                <div className="col-3">
                                                    <img className="home-item-profile-img " src={PREV_IMG + val.user.user_profile_photo} onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }} />
                                                </div>
                                                <div className="col-8">
                                                    <h6 className="home-item-profile-name ">{val.user.user_name}</h6>
                                                    <h6 className="home-item-post-time ">{val.added_date_str}</h6>
                                                </div>
                                            </div>
                                            <Link to={"/product_details/"+convertToBase64(val.id)} className="special-link">
                                                <p className="special-img">
                                                    <img className="home-item-img" src={PREV_IMG + val.default_photo.img_path} alt="" onError={(e) => { e.target.src = "./assets/images/placeholder.jpeg" }} />
                                                </p>
                                                <h3><span>{val.title}</span></h3>
                                            </Link>
                                            <div className="row home-item-info">
                                                <div className="col-12">
                                                    <h6 className="special-categ">{"$ " + val.price} ({val.condition_of_item.name})</h6>
                                                </div>
                                                <div className="col-12">
                                                    <span className="home-item-icon"><FontAwesomeIcon icon={faMapMarked} /></span>
                                                    {val.item_location.name}
                                                </div>

                                            </div>
                                            {/* <p className="home-item-location">
                                               </p> */}
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                        <p className="special-more">
                            <Link to="/products">show more</Link>
                        </p>
                        <span className="special-line1"></span>
                        <span className="special-line2"></span>
                    </div>
                </div>


                {/* <!-- Get a Special Deals --> */}

                <div className="getspec-wrap">
                    <div className="cont getspec">
                        <div className="getspec-cont">
                            <h3>Winter is coming</h3>
                            <p>New snowmobile parts</p>
                            <form action="#" className="form-validate">
                                <input data-required="text" data-required-email="email" type="text" placeholder="Email address" name="email3" />
                                <input type="submit" value="Get a special deals" />
                            </form>
                        </div>
                        <a href="#" className="getspec-img">
                            <img src="/external_assets/img/getspec.jpg" alt="" />
                        </a>
                    </div>
                </div>
                <div className="row" style={{ width: "100%",height:"70%" }}>
                    <BlogSlider blogs={blogs} />
                </div>

                {/* <!-- Frontpage Article --> */}
                <div className="botarticle-wrap row">
                    {/* Blogs */}

                    <div className="cont botarticle">
                        <div className="botarticle-cont">
                            <h3>Kings of the dirt</h3>
                            <p>Best motocross gear</p>
                            {/* <a href="#" className="botarticle-more">Read more</a> */}
                        </div>
                        <a href="blog.html" className="botarticle-img">
                            <img src="/external_assets/img/article1.jpg" alt="" />
                        </a>
                    </div>
                </div>


            </main>
        </div>
    );
}

export default Home;

